<template>
	<div class="orderLog">
    <div>
      <CommonHeader></CommonHeader>
    </div>
    <!--tab导航部分-->
    <div class="fix">
      <Set></Set>
    </div>

    <!--主要内容内容-->
    <div class="P-content">
      <!--页面头部分-->
      <div class="P-header">
        <div class='P-header-Text'>订单操作日志</div>
        <span class='border-cover'></span>
      </div>

      <!--查询条件-->
      <div class="P-search">
        <span class="demonstration">操作时间：</span>
        <el-date-picker v-model="orderLog.startTime" type="date" @change="dateOne"  :editable="false" placeholder="选择开始日期" :picker-options="pickerOptions0"></el-date-picker>
        <el-date-picker v-model="orderLog.endTime" type="date" @change="dateTwo" :editable="false" placeholder="选择结束日期" ></el-date-picker>

        <el-select v-model="orderLog.status" value="" class="" placeholder="请选择订单状态">
          <el-option v-for="item in statusList" :key="item.value" :label="item.name" :value="item.value"></el-option>
        </el-select>

        <el-input v-model="orderLog.search" class="search-input" placeholder="订单编号、协诊专家、主治医生"></el-input>
        <button type="button" class='btn btn-primary searchBtn' @click='search'>搜索</button>
      </div>

      <!--列表显示-->
      <div class="P-table">
        <table class="table table-bordered table-hover">
          <thead>
            <tr>
              <th>协诊单号</th>
              <th>状态</th>
              <th>协诊专家</th>
              <th>主治医生</th>
              <th>医院名称</th>
              <th>预约时间</th>
              <th>操作时间</th>
              <th>操作人</th>
            </tr>
          </thead>

          <tbody>
            <tr v-if="this.orderLogList == null || this.orderLogList.length == 0">
              <td colspan="8">
                <span>暂无数据</span>
              </td>
            </tr>
            <tr v-for="item in orderLogList">
              <td>{{ item.orderNo }}</td>
              <td>{{ item.statusText }}</td>
              <td>{{ item.expertName }}</td>
              <td>{{ item.doctorName }}</td>
              <td>{{ item.hospitalName }}</td>
              <td v-if="item.startTime != null">{{ item.startTime | dataTimeFilter}} ~ {{ item.endTime | timeFilter}}</td>
              <td v-else=""></td>
              <td>{{ item.optTime | dataTimeFilter}}</td>
              <td>{{ item.operator }}</td>
            </tr>
          </tbody>
        </table>
      </div>

      <!--翻页组件-->
      <flip @customEvent='customEvent' :Body='pageTemplater' :Condition='orderLog'></flip>
      <!--翻页组件-->
    </div>
	</div>
</template>

<style scoped>
  .P-content{
    margin:50px 15px 0px 195px;
    min-width:960px;
  }
  .P-header {
    padding:8px 0px;
    font-size:16px;
    height:50px;
    background:white;
    position:relative;
    border-bottom:1px solid #ddd;
  }
  .P-header-Text{
    padding:8px 8px 8px 8px;
  }
  .P-header .border-cover{
    display:block;
    width:1px;
    height:16px;
    background:#6886d4;
    position:absolute;
    left:0px;
    top:20px;
  }
  .P-search{
    margin-top:15px;
    margin-bottom:5px;
  }
  .search-input {
    width: 220px;
  }

  /*列表*/
  .P-table {
    margin-top: 30px;
  }
  .P-table table th,.P-table table{
    text-align:center;
  }
  .P-table .table thead{
    background:#f5f5f5;
  }
  .P-table .table tr{
    text-align:center;
  }
  .P-table .table td{
    padding:4px 0px;
    line-height:39px;
  }
  .P-table .table th{
    text-align:center;
    border-bottom:none;
  }
</style>

<script>
  //header
  import CommonHeader from '../public/header.vue';
  //menu
  import Set from '../public/set.vue';
  import moment from 'moment'
  import flip from '../public/flip.vue';//翻页组件

	export default {
    name: 'OrderLog',
    data: function () {
      return {
        orderLog: {
          startTime:'',
          endTime:'',
          status:'3',
          search:'',
          pageNumber:1,//当前页
          pageSize:10,//页面数据大小
        },
        pageTemplater: {},
        parentId:67,//订单状态数据字典ID
        statusList:[],
        orderLogList:[],
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() > Date.now() - 8.64e7;
          }
        },
      }
    },
    mounted() {
      this.getServerDate();//获取近一周时间时间
      this.queryStatusDictionary();//获取数据字典状态
    },
    methods: {
      //获取服务器时间
      getServerDate: function () {
        var url = "/api/basic/common/time?format="+this.formatDate;
        var token = localStorage.getItem('access_token');
        this.$http.get(url,{
          headers:{
            Authorization: 'bearer ' + token
          }
        }).then(function (res) {
          //返回成功，设置一周的日期
          this.orderLog.startTime = moment(new Date(res.bodyText) - 1000 * 60 * 60 * 24 * 7).format('YYYY-MM-DD')+' 00:00:00';
          this.orderLog.endTime = res.bodyText+' 23:59:59';

          //调用查询
          this.queryOrderLog();
        },function(err) {
          //返回失败
          this.orderLog.startTime = '';
          this.orderLog.endTime = '';
        })
      },
      //查询状态数据字典
      queryStatusDictionary: function () {
        var url = "/api/basic/dictionary/parent?parentId="+this.parentId;
        this.$http.get(url,{
          headers:{
            Authorization: ''
          }
        }).then(function (res) {
          //返回成功
          this.statusList = res.body;
          this.statusList.unshift({ name: '全部状态', value: '' })
        },function(err) {
          //返回失败
          this.statusList = [];
        })
      },
      search: function () {
        this.orderLog.pageNumber = 1;
        this.queryOrderLog();
      },
      //查询方法
      queryOrderLog: function () {
//        if(this.orderLog.startTime!='') this.orderLog.startTime = moment(this.orderLog.startTime).format('YYYY-MM-DD')+' 00:00:00';
//        if(this.orderLog.startTime!='') this.orderLog.endTime = moment(this.orderLog.endTime).format('YYYY-MM-DD')+' 23:59:59';
        var url = "/api/assist-order/orderLog/findByPage";
        var token = localStorage.getItem('access_token');

        this.$http.get(url, {
          headers: {
            Authorization: 'bearer ' + token
          },
          params:this.orderLog
        }).then(function (res) {
          //查询成功
          this.orderLogList = res.body.list;
          this.pageTemplater = res.body;
        },function (err) {
          //查询失败
          this.orderLogList = [];
        })
      },
      // 翻页事件
      customEvent() {
        this.queryOrderLog();
      },
      // 解决前大后
      dateOne() {
        if (new Date(this.orderLog.startTime) >= new Date(this.orderLog.endTime)) {
          var t = new Date(this.orderLog.startTime) + 1000 * 60 * 60 * 24
          this.orderLog.endTime = moment(t).format('YYYY-MM-DD')
        }
      },
      // 解决前大后
      dateTwo() {
        if (new Date(this.orderLog.startTime) >= new Date(this.orderLog.endTime)) {
          var t = new Date(this.orderLog.startTime) + 1000 * 60 * 60 * 24
          this.orderLog.endTime = moment(t).format('YYYY-MM-DD')
        }
      },
    },
    components: {
      CommonHeader: CommonHeader,
      Set: Set,
      flip:flip
    }
  }
</script>



